<template>
  <div>
    <!---->
    <div style="margin-bottom: 10px">
      <el-button type="danger" size="mini" @click="batchDelete()"></el-button>
    </div>
    <!---->
    <el-table :data="dataShow" border class="table" width="100%" max-height="750"
              ref="multipleTable" header-cell-class-name="table-header"
              @selection-change="handleSelectionChange">
      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column prop="taskId" label="ID" width="220" sortable></el-table-column>
      <el-table-column label="" width="120" :show-overflow-tooltip="true">
        <template #default="scope">
          <span v-if="scope.row.taskName != null">{{ scope.row.taskName }}</span>
          <span v-else>null</span>
        </template>
      </el-table-column>
      <el-table-column label="" width="100">
        <template #default="scope">
          <el-tag :type=taskState[scope.row.taskState].color size="small" :show-overflow-tooltip="true">{{taskState[scope.row.taskState].zh}}</el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="pluginId" label="ID" width="280"></el-table-column>
<!--      <el-table-column prop="mydataharborCreatorClazz" label="" width="150" :show-overflow-tooltip="true"></el-table-column>
      <el-table-column prop="groupName" label="" width="100"></el-table-column>-->
      <el-table-column label="" width="120">
        <template #default="scope">
          <el-tag v-if="scope.row.enableRebalance" size="small">true</el-tag>
          <el-tag v-else size="small" type="warning">false</el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="totalNumberOfPipline" label="" width="90"></el-table-column>
      <el-table-column label="" width="144" sortable>
        <template slot-scope="scope">
          <span>{{ scope.row.createTime | FormatDate('yyyy-MM-dd HH:mm') }}</span>
        </template>
      </el-table-column>
      <el-table-column label="" width="144" sortable>
        <template slot-scope="scope">
          <span>{{ scope.row.lastUpdateTime | FormatDate('yyyy-MM-dd HH:mm') }}</span>
        </template>
      </el-table-column>
      <el-table-column label="tags" width="120" :show-overflow-tooltip="true">
        <template #default="scope">
          <span v-if="scope.row.tags != undefined">{{ scope.row.tags }}</span>
          <span v-else>null</span>
        </template>
      </el-table-column>
      <el-table-column label="" align="center" fixed="right" width="300">
        <template #default="scope">
          <el-button @click="taskDetail(scope.row)" type="text"></el-button>
          <el-button @click="taskUpdate(scope.row)" type="text" style="color: #67C23A"></el-button>
          <el-button @click="taskFork(scope.row)" type="text" style="color: #E6A23C">fork</el-button>
          <el-button @click="taskStates(scope.row)" type="text" style="color: #909399"></el-button>
          <el-button @click="taskDelete(scope.row)" type="text" style="color: #F56C6C"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!---->
    <div class="pagination">
      <el-pagination background layout="sizes, total, prev, pager, next" :current-page="currentPage"
                     :page-sizes="[5,10,20,50,100]" :page-size="pageSize" :total="pageTotal"
                     @size-change="handleSizeChange"
                     @current-change="handlePageChange"></el-pagination>
    </div>
    <!--fork-->
    <el-dialog :title="dialogName" :visible.sync="dialogFormVisible" width="42%">
      <el-form :model="form" label-position="left" label-width="70px">
        <el-row>
          <el-col :span="11">
            <el-form-item label="">
              <el-input v-model="form.groupName" autocomplete="off"
                        size="small" style="width: 80%" disabled></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="ID" prop="pluginId">
              <el-select v-model="form.pluginId" @change="selectPluginId" placeholder="ID"
                         size="small" style="width: 100%">
                <el-option v-for="item in pluginInstallList" v-bind:key="item.id" :label="item.pluginId" :value="item.pluginId"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="11">
            <el-form-item label="">
              <el-input v-model="form.totalNumberOfPipline" autocomplete="off"
                        size="small" style="width: 80%"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="">
              <el-select v-model="form.mydataharborCreatorClazz" placeholder="CreatorClazz"
                         size="small" style="width: 100%">
                <el-option v-for="item in clazzList" v-bind:key="item.id" :label="item" :value="item"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="11">
            <el-form-item label="">
              <el-select v-model="form.enableRebalance" placeholder="" size="small" style="width: 80%">
                <el-option  label=true value=true></el-option>
                <el-option  label=false value=false></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :space="14">
            <el-alert title="" type="info" show-icon></el-alert>
          </el-col>
        </el-row>
      </el-form>
      <p></p>
      <vue-json-editor v-model="form.settingJsonConfig" :mode="'code'" lang="zh"></vue-json-editor>
      <p></p>
      <vue-json-editor v-model="form.configJson" :mode="'code'" lang="zh"></vue-json-editor>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel"> </el-button>
        <el-button type="primary" @click="submit"> </el-button>
      </div>
    </el-dialog>

    <!---->
    <el-dialog title="" :visible.sync="taskDetailVisible" width="42%">
      <p></p>
      <el-table :data="taskDetailForm">
        <!---->
        <el-table-column type="expand">
          <template slot-scope="props">
            <p></p>
            <el-table :data="taskAssignedInfo">
              <!---->
              <el-table-column type="expand">
                <template slot-scope="props">
                  <p></p>
                  <p v-for="(value,key) in props.row.piplineStates">{{key}}{{ value }}</p>
                  <el-divider></el-divider>
                  <p></p>
                  <p v-for="(value,key) in props.row.writeTotal">{{key}}{{ value }}</p>
                </template>
              </el-table-column>
              <el-table-column prop="nodeName" label="" width="120"></el-table-column>
              <el-table-column prop="taskNum" label="" width="80"></el-table-column>
              <el-table-column label="diverted" width="80">
                <template #default="scope">
                  <el-tag v-if="scope.row.diverted" size="small">true</el-tag>
                  <el-tag v-else size="small" type="warning">false</el-tag>
                </template>
              </el-table-column>
              <el-table-column prop="createException" label="" :show-overflow-tooltip="true">
                <template #default="scope">
                  <span v-if="scope.row.createException == null"></span>
                  <span v-else>{{scope.row.createException}}</span>
                </template>
              </el-table-column>
            </el-table>
          </template>
        </el-table-column>
        <!---->
        <el-table-column prop="groupName" label="" width="80"></el-table-column>
        <el-table-column prop="taskId" label="ID" width="180" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column prop="pluginId" label="ID" width="200" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column prop="totalNumberOfPipline" label=""></el-table-column>
        <el-table-column label="">
          <template #default="scope">
            <el-tag v-if="scope.row.enableRebalance" size="small">true</el-tag>
            <el-tag v-else size="small" type="warning">false</el-tag>
          </template>
        </el-table-column>
      </el-table>
      <p></p>
      <vue-json-editor v-model="detailSettingJsonConfig" :mode="'code'" lang="zh"></vue-json-editor>
      <p></p>
      <vue-json-editor v-model="detailConfigJson" :mode="'code'" lang="zh"></vue-json-editor>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel"> </el-button>
      </div>
    </el-dialog>

    <!---->
    <el-dialog title="" :visible.sync="dialogUpdateVisible" width="42%">
      <el-form :model="updateForm" label-position="left">
        <el-row>
            <el-col :span="11">
              <el-form-item label="ID" label-width="70px">
                <el-input v-model="updateForm.taskId" autocomplete="off" style="width: 250px;" disabled></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="" label-width="70px" prop="pluginId">
                <el-input v-model="updateForm.totalNumberOfPipline" autocomplete="off" style="width: 250px;"></el-input>
              </el-form-item>
             </el-col>
             <el-col :span="11">
                <el-form-item label="" label-width="70px" prop="pluginId">
                  <el-input v-model="updateForm.taskName" autocomplete="off" style="width: 250px;"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label="" label-width="70px" prop="version">
                  <el-select v-model="updateForm.enableRebalance" placeholder="" style="width: 250px">
                    <el-option  label=true value=true></el-option>
                    <el-option  label=false value=false></el-option>
                  </el-select>
                </el-form-item>
              </el-col>
        </el-row>
      </el-form>
       <p>tags</p>
      <vue-json-editor v-model="updateForm.tags" :mode="'code'" lang="zh"></vue-json-editor>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel"> </el-button>
        <el-button type="primary" @click="updateFormSubmit"> </el-button>
      </div>
    </el-dialog>

    <!---->
    <el-dialog title="" :visible.sync="dialogStateVisible" width="23%">
      <el-form :model="stateForm" label-position="left">
        <el-form-item label="ID" label-width="70px">
          <el-input v-model="stateForm.taskId" autocomplete="off" style="width: 250px;" disabled></el-input>
        </el-form-item>
        <el-form-item label="" label-width="70px" prop="version">
          <el-select v-model="stateForm.taskState" placeholder="" style="width: 250px">
            <el-option v-for="(key, value) in states" :label="key" :value="value"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel"> </el-button>
        <el-button type="primary" @click="updateStateFormSubmit"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import vueJsonEditor from 'vue-json-editor'

export default {
  inject: ['reload'],
  name: "Tasks",
  //
  props: ['groupName'],
  data() {
    return {
      //
      taskList: [],
      //
      pluginInstallList: [],
      //
      clazzList: [],
      //
      dialogFormVisible: false,
      taskDetailVisible: false,
      dialogUpdateVisible: false,
      dialogStateVisible: false,

      //
      form: {
        groupName: this.groupName,
        pluginId: '',
        mydataharborCreatorClazz: '',
        totalNumberOfPipline: 1,
        enableRebalance: true,
        settingJsonConfig: {
          "parallel":false,
          "threadNum":1,
          "batch":false,
          "sleepTime":0
        },
        configJson: {},
        tags: {}
      },
      //
      taskDetailForm: [],
      detailSettingJsonConfig: {},
      detailConfigJson: {},
      taskAssignedInfo: [],

      //
      taskState: {
        created: {
          zh:'',
          color: ''
        },
        started: {
          zh:'',
          color: 'success'
        },
        suspend: {
          zh:'',
          color: 'info'
        },
        continued: {
          zh:'',
          color: 'warning'
        },
        over: {
          zh:'',
          color: 'over'
        }

      },
      states: {created:"",started:"",suspend:"",continued:"",over:""},
      //
      dialogName: "",
      //
      updateForm: {
        enableRebalance: true,
        taskId: "",
        taskName: "",
        totalNumberOfPipline: 0,
        tags: {}
      },
      //
      stateForm: {
        taskId: "",
        taskState: ''
      },
      /**/
      // 
      totalPage: [],
      // 
      pageSize: 10,
      // 
      pageTotal: 1,
      // 
      dataShow: [],
      // 
      currentPage: 0,

      //
      multipleSelection: []
    }
  },
  components: {
    vueJsonEditor
  },
  mounted() {
    this.initData();
    this.getTasksByGroupName();
  },
  methods: {
    //
    clearData() {
      this.form = {
        groupName: this.groupName,
        pluginId: '',
        mydataharborCreatorClazz: '',
        totalNumberOfPipline: 1,
        enableRebalance: true,
        settingJsonConfig: {
          "parallel":false,
          "threadNum":1,
          "batch":false
        },
        configJson: {

        }
      };
      this.taskDetailForm = [];
      this.dialogName = "";
    },
    //
    initData() {
      this.taskList = [];
      //
      this.postRequest("mydataharbor/task/listTask").then(res=>{
        if (res.code == 0) {
          let allTask = res.data;
          for (let resKey in allTask) {
            this.taskList.push(allTask[resKey]);
          }
          this.initPageData();
        }
      });
    },
    //
    getTasksByGroupName() {
      this.getRequest("/mydataharbor/node/plugin?groupName=" + this.groupName).then(res=> {
        this.pluginInstallList = res.data;
        //console.log(this.pluginInstallList);
      })
    },
    //
    submit() {
      this.dialogFormVisible = false;
      this.form.configJson = JSON.stringify(this.form.configJson);
      this.form.settingJsonConfig = JSON.stringify(this.form.settingJsonConfig);
      this.postRequest("mydataharbor/task/submit", this.form).then(res=>{
        if (res.code == -1) {
          this.$message.error(res.msg);
        }

        if (res.code == 0) {
          this.$message.info("fork");
          this.initData();
          this.clearData();
        }
      })
    },
    cancel() {
      this.dialogFormVisible = false;
      this.taskDetailVisible = false;
      this.dialogUpdateVisible = false;
      this.dialogStateVisible = false;
      this.clearData();
    },
    //
    selectPluginId(value) {
      this.clazzList = [];
      this.pluginInstallList.forEach(plugin=>{
        if (plugin.pluginId == value) {
          plugin.dataSinkCreatorInfos.forEach(dataSink=>{
            this.clazzList.push(dataSink.clazz);
          })
        }
      })
    },
    //
    taskDetail(task) {
      this.taskDetailForm = [];
      this.taskAssignedInfo = [];

      let row = JSON.parse(JSON.stringify(task));
      this.detailConfigJson = JSON.parse(task.configJson);
      this.detailSettingJsonConfig = JSON.parse(task.settingJsonConfig);
      //
      let assignedInfo = task.taskAssignedInfo.assignedInfoMap;
      for (let rowKey in assignedInfo) {
        this.taskAssignedInfo.push(assignedInfo[rowKey]);
      }
      console.log(this.taskAssignedInfo);
      this.taskDetailForm.push(row);
      console.log(this.taskDetailForm);
      this.taskDetailVisible = true;
    },
    //
    taskUpdate(task) {
      this.updateForm.taskId = task.taskId;
      this.updateForm.taskName = task.taskName;
      this.updateForm.totalNumberOfPipline = task.totalNumberOfPipline;
      this.updateForm.enableRebalance = task.enableRebalance;
      this.updateForm.tags = task.tags;
      this.dialogUpdateVisible = true;
    },
    //
    updateFormSubmit() {
      this.postRequest("mydataharbor/task/editTask", this.updateForm).then(res=>{
        if (res.code == 0) {
          this.$message.info("");
          this.initData();
        } else {
          this.$message.warning(res.msg);
        }
      });
      this.dialogUpdateVisible = false;
    },
    //
    taskFork(task) {
      this.dialogName = "Fork";
      this.dialogFormVisible = true;
      this.form = JSON.parse(JSON.stringify(task));
      this.form.configJson = JSON.parse(task.configJson);
      this.form.settingJsonConfig = JSON.parse(task.settingJsonConfig);
    },
    //
    taskStates(task) {
      this.stateForm.taskId =  task.taskId;
      this.stateForm.taskState = task.taskState;
      this.dialogStateVisible = true;
    },
    //
    taskDelete(task) {
      this.$confirm('' + task.taskId, '', {
          distinguishCancelAndClose: true,
          confirmButtonText: '',
          cancelButtonText: ''
        })
        .then(() => {
          this.postRequest("/mydataharbor/task/deleteTask?taskId=" + task.taskId).then(res=>{
            if(res.code == 0) {
              this.$message({
                type: 'info',
                message: ''
              });
            } else {
              this.$message({
                type: 'warning',
                message: '' + res.msg
              });
            }
          })
          this.reload();
        });
    },
    //
    updateStateFormSubmit() {
      this.postRequest("mydataharbor/task/manageTaskState?taskId=" + this.stateForm.taskId + "&taskState=" + this.stateForm.taskState).then(res=>{
        if (res.code == 0) {
          this.$message.info("");
          this.initData();
        } else {
          this.$message.error(res.msg);
        }
      });
      this.dialogStateVisible = false;
    },
    //
    initPageData() {
      this.pageTotal =this.taskList.length;
      for (let i = 0; i < this.pageTotal; i++) {
        //   [[''],[''],['']]
        //   ,pageSize5 1-5slice(0,5)6-10slice(5,10)...
        this.totalPage[i+1] = this.taskList.slice(this.pageSize * i, this.pageSize * (i + 1))
      }
      this.handlePageChange(1);
    },
    handlePageChange(pageNo) {
      this.currentPage = pageNo;
      // 
      this.dataShow = this.totalPage[this.currentPage];
    },
    handleSizeChange(size){
      this.pageSize = size;
      this.initPageData();
    },
    //
    handleSelectionChange(val) {
      this.multipleSelection = val
    },
    //
    batchDelete() {
      console.log(this.multipleSelection)
      if (this.multipleSelection.length == 0){
        this.$message.info("");
        return
      }
      let deleteCount = 0;
      this.$confirm('' + this.multipleSelection.length + '', '', {
          distinguishCancelAndClose: true,
          confirmButtonText: '',
          cancelButtonText: ''
        })
        .then(() => {
          this.multipleSelection.forEach(task=>{
            //console.log(task.taskId)
            this.postRequest("/mydataharbor/task/deleteTask?taskId=" + task.taskId).then(res=>{
              if(res.code == 0) {
                deleteCount = deleteCount+1;
                if(deleteCount == this.multipleSelection.length) {
                  this.$message({
                      type: 'info',
                      message: ''+ deleteCount +''
                  });
                }
              }
            })
          })
          this.reload();
        });
    }
  }
}
</script>

<style scoped>
/deep/ .el-col-24 {
  width: 50%;
  margin-left: -6%;
}
/deep/ .el-alert--info.is-light {
  background-color: #ffffff;
}
.demo-table-expand {
  font-size: 0;
}
.demo-table-expand label {
  width: 90px;
  color: #99a9bf;
}
.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 50%;
}
</style>



