<template>
  <div>
      <el-tag> {{groupName}}</el-tag>

      <el-button type="danger" size="small" style="margin-left:20px" @click="deleteGroup()"></el-button>
  </div>
</template>

<script type="text/javascript">

export default {
  inject: ['reload'],
  name: "operation",
  //
  props: ['groupName'],
  data() {
    return {

    }
  },
  methods: {
    deleteGroup() {
      this.$confirm('' + this.groupName + ', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.getRequest("/mydataharbor/node/deleteGroup?groupName=" + this.groupName).then(res=>{
            console.log(res);
            if (res.code == 0) {
              this.$message({
                type: 'success',
                message: '!'
              });
              // 
              this.reload();
            } else {
              this.$message({
                type: 'error',
                message: '' + res.msg
              });
            }
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '!'
          });
        });

    }
  }
}
</script>

<style scoped>

</style>



